import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, RouterStateSnapshot } from '@angular/router';
import { Http, Response } from '@angular/http';
import * as jwt_decode from 'jwt-decode';
import { MatDialog, MatSnackBar, MatDialogRef } from '@angular/material';
import { Router } from '@angular/router';
import { DialogComponent } from '../../app/modules/user/Dialog/DialogComponent';
@Injectable()
export class LoginGuard implements CanActivate {
  token: string;

  constructor(private router: Router, private dialog: MatDialog
  ) { }

  public canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
    debugger
    var flagIsCanActive = false;
    let token = localStorage.getItem('id_token');
    if (token == "undefined" || token == null) {
      if (state.url != '/') {
       window.open('http://sso-bepartners-dev.apps.appcanvas.net/login','_self'); 
      }
      
    }
    else {
      var decoded = jwt_decode(token)
      var dateToken = new Date(decoded.exp * 1000);
      var dateNow = new Date(Date.now());
      if (dateNow > dateToken) {
        if (state.url != '/') {
          this.redirect();
        }
        localStorage.setItem('isLoggedIn', "false");
      }
      else {
        localStorage.setItem('currentUser', decoded.sub);
        flagIsCanActive = true;
        localStorage.setItem('isLoggedIn', "true");
      }
      return flagIsCanActive;
    }
  }
  redirect() {
    this.dialog.open(DialogComponent, { data: 5 }).afterClosed().subscribe(result => {
             window.open('http://sso-bepartners-dev.apps.appcanvas.net/login','_self'); 

    });
  }
}