import {FactoryProvider, InjectionToken} from "@angular/core";
import {IRepository} from "../../../../domain/interfaces/irepository.interface";
import {PersonLinkedService} from "./personlinked.service";
import {PersonLinkedModel} from '../../../../domain/models/profile/personlinkedmodel.model';
import { PersonLinkedModelRequest } from '../../../../domain/models/profile/personlinkedmodelRequest.model';

export const PersonLinkedToken = new InjectionToken<IRepository<PersonLinkedModelRequest>>('personlinked.service');

export class PersonLinkedProvider implements FactoryProvider {
  public provide: InjectionToken<IRepository<PersonLinkedModelRequest>> = PersonLinkedToken;
  public deps: any[] = [PersonLinkedService];
  public multi: boolean = false;
  public useFactory: Function = (...deps: IRepository<PersonLinkedModelRequest>[]) => {
    return deps[0]
  };
}