
import { FactoryProvider, InjectionToken } from "@angular/core";
import { IRepository } from "../../../../domain/interfaces/irepository.interface";
import { ProfileBasicUpSertService } from "./profilebasicupsert.service";
import { ProfileBasic } from '../../../../domain/models/profile/profilebasic.model';

export const ProfileBasicUpsertToken = new InjectionToken<IRepository<ProfileBasic>>('profilebasicupsert.service');

export class ProfileBasicUpSertProvider implements FactoryProvider {
	public provide: InjectionToken<IRepository<ProfileBasic>> = ProfileBasicUpsertToken;
	public deps: any[] = [ProfileBasicUpSertService];
	public multi: boolean = false;
	public useFactory: Function = (...deps: IRepository<ProfileBasic>[]) => {
		return deps[0]
	};
}