import { Observable } from 'rxjs';
import { RootObject } from '../../../../domain/models/register/rootobject.model';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { IRepository } from '../../../../domain/interfaces/irepository.interface';
import { AppSettings } from '../../../../domain/appSettings';
import { BranchModel } from '../../../../domain/models/profile/branchmodel.model';
import {UrlPath} from '../../../../domain/enums/urlpath';
import {LogOut} from '../../../../domain/models/logout/logout';

@Injectable()
export class LogOutService implements IRepository<LogOut> {

	constructor(private http: HttpClient) {
	}

	public create(values: LogOut): Observable<LogOut> {
		var request = this.http.post<LogOut>(UrlPath.URL_PATH+`/servicebroker/v1/release/oauth/logout/`, values);
        return request; 
	}


}
