import {Component, Inject, OnInit} from '@angular/core';
import {IRepository} from '../../../../../domain/interfaces/irepository.interface';
import {HistorySearchToken} from '../../../services/prestaclic/historysearch.provider';
import {RequestHistorySearch} from '../../../../../domain/models/plans/requesthistorysearch.model';
import {ResponseHistorySearch} from '../../../../../domain/models/plans/responsehistorysearch.model';
import {UserSession} from '../../../../../domain/models/profile/usersession.model';

import { CommunicationService } from 'src/client/app/services/Communication.service';


@Component({
  selector: 'app-misconsultas',
  templateUrl: './misconsultas.component.html',
  styleUrls: ['./misconsultas.component.css']
})
export class MisConsultasComponent implements OnInit {

  public listHistoric : ResponseHistorySearch[];
  public request=new RequestHistorySearch();
  public limit : number = 0;
  public offset : number = 100;
  public userSession=new UserSession();

  constructor(@Inject(HistorySearchToken) private historySearchIRepository: IRepository<RequestHistorySearch>, private comService: CommunicationService) { }

  ngOnInit() {
    this.userSession=JSON.parse(sessionStorage.getItem('userSession'));
    this.loadHistoric();

    this.comService.change.subscribe(reload=>{
      this.loadHistoric();      
      console.log('refreshh',reload);
    });

  }

  loadHistoric(){
    this.request.nLimit= this.limit;
    this.request.nOffset = this.offset;
    this.request.vIdThirdPublic = this.userSession.idThirdPublic;
    this.historySearchIRepository.create(this.request).subscribe(response => {
      this.listHistoric = response.content;
     // console.log(JSON.stringify(this.listHistoric));
    });
  }

}
