import {FactoryProvider, InjectionToken} from "@angular/core";
import {IRepository} from "../../../../domain/interfaces/irepository.interface";
import {Example} from "../../../../domain/models/example/RootObject";
import {ExampleService} from "../example/example.service";

export const ExampleToken = new InjectionToken<IRepository<Example>>('example.service');

export class ExampleProvider implements FactoryProvider {   
  public provide: InjectionToken<IRepository<Example>> = ExampleToken;
  public deps: any[] = [ExampleService];
  public multi: boolean = false;
  public useFactory: Function = (...deps: IRepository<Example>[]) => {
    return deps[0]
  };
}