import { FactoryProvider, InjectionToken } from "@angular/core";
import { IRepository } from "../../../../domain/interfaces/irepository.interface";
import { ThirdService } from "./third.service";
import {UserSessionService} from './usersession-service';
import {User} from '../../../../domain/models/register/user.model';

export const UserSessionToken = new InjectionToken<IRepository<User>>('usersession.service');

export class UserSessionProvider implements FactoryProvider {
  public provide: InjectionToken<IRepository<User>> = UserSessionToken;
  public deps: any[] = [UserSessionService];
  public multi: boolean = false;
  public useFactory: Function = (...deps: IRepository<User>[]) => {
    return deps[0]
  };
}
