
import {FactoryProvider, InjectionToken} from "@angular/core";
import {IRepository} from "../../../../domain/interfaces/irepository.interface";

import {HistorySearchService} from './historysearch.service';
import {RequestHistorySearch} from '../../../../domain/models/plans/requesthistorysearch.model';

export const HistorySearchToken = new InjectionToken<IRepository<RequestHistorySearch>>('historysearch.service');

export class HistorySearchProvider implements FactoryProvider {
  public provide: InjectionToken<IRepository<RequestHistorySearch>> = HistorySearchToken;
  public deps: any[] = [HistorySearchService];
  public multi: boolean = false;
  public useFactory: Function = (...deps: IRepository<RequestHistorySearch>[]) => {
    return deps[0]
  };
}
