import {Observable} from 'rxjs';
import {RootObject} from '../../../../domain/models/register/rootobject.model';
import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {IRepository} from '../../../../domain/interfaces/irepository.interface';
import {UrlPath} from '../../../../domain/enums/urlpath';

@Injectable()
export class ChangePhotoService implements IRepository<FormData> {

  constructor(private http: HttpClient) {
  }

  public create(values: FormData): Observable<RootObject> {
    var request = this.http.post<RootObject>(UrlPath.URL_PATH + `/servicebroker/v1/private/catalog/profile/changeimageprofile/`, values);
    return request;
  }

}

