import {Component, Inject, OnInit, ViewChild} from '@angular/core';
import {Router} from '@angular/router';
import {PcPlansToken} from '../../services/plans/pcplans.provider';
import {IRepository} from '../../../../domain/interfaces/irepository.interface';
import {RequestPlan} from '../../../../domain/models/plans/requestplan.model';
import {BoPCPlan} from '../../../../domain/models/plans/bopcplan.model';
import {
    SwiperComponent,
    SwiperDirective,
    SwiperConfigInterface,
    SwiperScrollbarInterface,
    SwiperPaginationInterface
} from 'ngx-swiper-wrapper';

@Component({
  selector: 'app-pricing',
  templateUrl: './pricing.component.html',
  styleUrls: ['./pricing.component.scss']
})
export class PricingComponent implements OnInit {

  public listBoPCPlan: BoPCPlan[] = [];
  public listBoPCPlanTemp: BoPCPlan[] = [];

  constructor(@Inject(PcPlansToken) private boPCPlanIRepository: IRepository<RequestPlan>, private router: Router) {
  }

  ngOnInit() {
    this.loadPlans();
  }

  consultarAhora() {
    this.router.navigate(['products/pcplanselection']);
  }

  loadPlans() {
    var request = new RequestPlan();
    this.boPCPlanIRepository.create(request).subscribe(response => {
      this.listBoPCPlanTemp = response.content;
      
      sessionStorage.setItem('listBoPCPlan', JSON.stringify(this.listBoPCPlanTemp));
      this.listBoPCPlan = [];
      this.listBoPCPlan = this.listBoPCPlanTemp;
      /*
      this.listBoPCPlan.push(this.listBoPCPlanTemp[0]);
      this.listBoPCPlan.push(this.listBoPCPlanTemp[2]);
      this.listBoPCPlan.push(this.listBoPCPlanTemp[3]);*/
    });
  }



  public config_thumbs = {
    a11y:true,
    direction: 'horizontal',
    observer: true,

    spaceBetween: 10,
    slideToClickedSlide: true,
    slidesPerView: 3,
    slidesOffsetBefore: 20,
    slidesOffsetAfter: 20,
    simulateTouch: true,
    resistanceRatio: 0.6,
    watchSlidesVisibility: true,
    watchSlidesProgress: true,
    watchOverflow: true,
    navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev',
      },
    breakpoints: {
        // when window width is <= 1024px
        1024: {
            slidesPerView: 2
        },
        778: {
          slidesPerView: 1
        }
       
    }

    
  };




  public config: SwiperConfigInterface = {
    a11y: true,
    direction: 'horizontal',
    loop: true,
    slidesPerView: 1,
    keyboard: true,
    mousewheel: true,
    scrollbar: false,
    navigation: false,
    pagination: true,
    
  };

  private scrollbar: SwiperScrollbarInterface = {
      el: '.swiper-scrollbar',
      hide: true,
      draggable: true,
  };

  private pagination: SwiperPaginationInterface = {
      el: '.swiper-pagination',
      clickable: true,
      hideOnClick: false
  };

  


}
