import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.css']
})
export class IndexComponent implements OnInit {
  tab1:boolean = true;
	tab2:boolean = false;
	tab3:boolean = false;

  constructor() { }

  ngOnInit() {
  }

	tabs(opt){
		if( opt == 1 ){
		  this.tab1 = true;
		  this.tab2 = false;
		  this.tab3 = false;
		}
		if( opt == 2 ){
		  this.tab1 = false;
		  this.tab2 = true;
		  this.tab3 = false;
		}
		if( opt == 3 ){
		  this.tab1 = false;
		  this.tab2 = false;
		  this.tab3 = true;
		}
	}

}
