import { FactoryProvider, InjectionToken } from "@angular/core";
import { IRepository } from "../../../../domain/interfaces/irepository.interface";
import {ChangePhotoService} from './changephoto.service';

export const ChangePhotoToken = new InjectionToken<IRepository<FormData>>('changephoto.service');

export class ChangePhotoProvider implements FactoryProvider {
  public provide: InjectionToken<IRepository<FormData>> = ChangePhotoToken;
  public deps: any[] = [ChangePhotoService];
  public multi: boolean = false;
  public useFactory: Function = (...deps: IRepository<FormData>[]) => {
    return deps[0]
  };
}
