import { Observable } from 'rxjs';
import { RootObject } from '../../../../domain/models/register/rootobject.model';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { IRepository } from '../../../../domain/interfaces/irepository.interface';
import { AppSettings } from '../../../../domain/appSettings';
import { ProfileBasic } from '../../../../domain/models/profile/profilebasic.model';
import {UrlPath} from '../../../../domain/enums/urlpath';


@Injectable()
export class ProfileBasicUpSertService implements IRepository<ProfileBasic> {

	constructor(private http: HttpClient) {
	}

	public create(values: ProfileBasic): Observable<RootObject> {
		var request = this.http.post<RootObject>(UrlPath.URL_PATH+`/servicebroker/v1/private/catalog/profile/upsertProfileBasic/`, values);
		return request;
	}

}
