import { Observable } from 'rxjs';
import { RootObject } from '../../../../domain/models/register/rootobject.model';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { IRepository } from '../../../../domain/interfaces/irepository.interface';
import { AppSettings } from '../../../../domain/appSettings';
import { ProfileRequest } from '../../../../domain/models/profile/profilerequest.model';
import {UrlPath} from '../../../../domain/enums/urlpath';

@Injectable()
export class ProfileBasicService implements IRepository<ProfileRequest> {

	constructor(private http: HttpClient) {
	}

	public sendRequest(values: ProfileRequest): Observable<RootObject> {
		var request = this.http.post<RootObject>(UrlPath.URL_PATH+`/servicebroker/v1/private/catalog/profile/profileBasic/`, values);
		return request;
	}

}

