import {FactoryProvider, InjectionToken} from "@angular/core";
import {IRepository} from "../../../../domain/interfaces/irepository.interface";
import {catalog} from "../../../../domain/models/register/catalog.model";
import {CatalogService} from "./catalog.service";

export const CatalogToken = new InjectionToken<IRepository<catalog>>('catalog.service');

export class CatalogProvider implements FactoryProvider {   
  public provide: InjectionToken<IRepository<catalog>> = CatalogToken;
  public deps: any[] = [CatalogService];
  public multi: boolean = false;
  public useFactory: Function = (...deps: IRepository<catalog>[]) => {
    return deps[0]
  };
}