import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {IndexProfileComponent} from '../profile/index/indexprofile.component';
import {IndexEditComponent} from '../profile/indexedit/indexedit.component';
import {CanActivate} from '@angular/router';
import {LoginGuard} from '../../guards/login.guard';
import {ManageWelcomeComponent} from './managewelcome/managewelcome.component';

const routes: Routes = [
  {path: 'perfilindex', component: IndexProfileComponent, canActivate: [LoginGuard]},
  {path: 'edit', component: IndexEditComponent, canActivate: [LoginGuard]},
  {path: 'redirectPublic', component: ManageWelcomeComponent}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})

export class ProfileRoutingModule {
}
