import {FactoryProvider, InjectionToken} from "@angular/core";
import {IRepository} from "../../../../domain/interfaces/irepository.interface";
import {RootObject} from "../../../../domain/models/register/rootobject.model";
import {StatusService} from "./status.service";
import {Register} from '../../../../domain/models/register/register.model';

export const StatusToken = new InjectionToken<IRepository<Register>>('status.service');

export class StatusProvider implements FactoryProvider {
  public provide: InjectionToken<IRepository<Register>> = StatusToken;
  public deps: any[] = [StatusService];
  public multi: boolean = false;
  public useFactory: Function = (...deps: IRepository<Register>[]) => {
    return deps[0]
  };
}