import {FactoryProvider, InjectionToken} from "@angular/core";
import {IRepository} from "../../../../domain/interfaces/irepository.interface";
import {FinantialService} from "./finantial.service";
import {FinantialModel} from '../../../../domain/models/profile/finantialmodel.model';

export const FinantialToken = new InjectionToken<IRepository<FinantialModel>>('finantial.service');

export class FinantialProvider implements FactoryProvider {
  public provide: InjectionToken<IRepository<FinantialModel>> = FinantialToken;
  public deps: any[] = [FinantialService];
  public multi: boolean = false;
  public useFactory: Function = (...deps: IRepository<FinantialModel>[]) => {
    return deps[0];
  };
}

