import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { IsLoginModule } from '../layautshared/islogin.module';

import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { LayoutComponent } from './layout/layout.component';
import { ScrollToModule } from '@nicky-lenaers/ngx-scroll-to';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { ScrollSpyModule } from 'ngx-scrollspy';
import {ExampleService} from '../services/example/example.service';
import {ExampleProvider} from '../services/example/example.provider';
import {ProfileBasicService} from '../services/profile/profilebasic.service';
import {ProfileBasicProvider} from '../services/profile/profilebasic.provider';
import {LogOutService} from '../services/sso/logout.service';
import {HistorySearchProvider} from '../services/sso/logout.provider';
import {GestorConsultaModule} from '../modules/gestorconsulta/gestorconsulta.module';



@NgModule({
  declarations: [HeaderComponent, FooterComponent, LayoutComponent],
  imports: [
    BrowserModule,
    CommonModule,
    RouterModule,
    NgbModule,
    ScrollSpyModule.forRoot(),
    ScrollToModule.forRoot(),
    IsLoginModule
  ],
  providers: [
    //Correspondian a LayoutSession
    ExampleService,
    new ExampleProvider(),
    ProfileBasicService,
    new ProfileBasicProvider(),
    LogOutService,
    new HistorySearchProvider()
  ]
})
export class LayoutModule { }
