import { Observable } from 'rxjs';
import { RootObject } from '../../../../domain/models/register/rootobject.model';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { IRepository } from '../../../../domain/interfaces/irepository.interface';
import { AppSettings } from '../../../../domain/appSettings';
import { PersonLinkedModel } from '../../../../domain/models/profile/personlinkedmodel.model';
import { PersonLinkedModelRequest } from '../../../../domain/models/profile/personlinkedmodelRequest.model';
import {UrlPath} from '../../../../domain/enums/urlpath';

@Injectable()
export class PersonLinkedService implements IRepository<PersonLinkedModelRequest> {

	constructor(private http: HttpClient) {
	}

	public create(values: PersonLinkedModelRequest): Observable<RootObject> {
		console.log('ANtes del envio');
		console.log(values);		
		var request = this.http.post<RootObject>(UrlPath.URL_PATH+`/servicebroker/v1/public/catalog/profile/person/save/list/`, values);
		return request;
	}

	public createList(values: PersonLinkedModelRequest[]): Observable<RootObject> {
		var request = this.http.post<RootObject>(UrlPath.URL_PATH+`/servicebroker/v1/private/catalog/profile/person/listoinfo/`, values);
		return request;
	}

}
