import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';
import {AppRoutingModule} from './app-routing.module';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {AppComponent} from './app.component';
import {LayoutModule} from './layout/layout.module';

import {RegisterModule} from './modules/user/user.module';
import {StartupService} from './services/startup.service';
import {HTTP_INTERCEPTORS, HttpClientModule} from '@angular/common/http';
import {LoginGuard} from './guards/login.guard';
import {InterceptorHttp} from './services/interceptorHttp.service';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';

//Start Material Design
import {MatButtonModule, MatCheckboxModule, MatInputModule} from '@angular/material';
import {MatCardModule} from '@angular/material/card';
import {MatIconModule} from '@angular/material/icon';
import {MatMenuModule} from '@angular/material/menu';
import {MatSidenavModule} from '@angular/material/sidenav';


import {CommunicationService} from './services/Communication.service';
import {PayPlanService} from './services/plans/payplan.service';
import {PayPlanProvider} from './services/plans/payplan.provider';
import {UserSessionService} from './services/profile/usersession-service';
import {UserSessionProvider} from './services/profile/usersession.provider';
import {ProfileHomeModule} from './modules/profile/profilehome.module';
import {DatePipe} from '@angular/common';
import {IsLoginModule} from './layautshared/islogin.module';
import {PagesModule} from './modules/pages.module';
import {HomeModule} from './modules/home/home.module';


//End Material Design

@NgModule({
  declarations: [
    AppComponent,
//    FooterComponent,
//    HeaderComponent,
//    LayoutComponent,
  ],
  imports: [
   // HomeModule,
    BrowserModule,
    LayoutModule,
    AppRoutingModule,
    RegisterModule,
    HttpClientModule,
    BrowserAnimationsModule,
    MatButtonModule,
    MatCheckboxModule,
    MatInputModule,
    MatCardModule,
    MatIconModule,
    MatMenuModule,
    MatSidenavModule,
    ReactiveFormsModule,
    FormsModule,
    ProfileHomeModule,
    PagesModule,
    IsLoginModule
  ],
  providers: [StartupService, LoginGuard,
    {
      provide: HTTP_INTERCEPTORS,
      multi: true,
      useClass: InterceptorHttp
    }, CommunicationService,
    UserSessionService,
    new UserSessionProvider(),
    [DatePipe]
  ],
  bootstrap: [AppComponent]
})
export class AppModule {
}
