import {Observable} from 'rxjs';
import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {IRepository} from '../../../../domain/interfaces/irepository.interface';
import {Example} from '../../../../domain/models/example/RootObject';


@Injectable()
export class ExampleService implements IRepository<Example> {

  constructor(private http: HttpClient) {
  }

  public create(Example:Example): Observable<Example> {
    var request= this.http.get<Example>(`https://jsonplaceholder.typicode.com/todos/1`);
     return request
  }

}
