import { Register } from "../models/register/register.model";
import { Third } from "../models/register/third.model";
import { ThirdInfo } from "../models/register/thirdInfo.model";
import { User } from "../models/register/user.model";
import { UserInfo } from "../models/register/userInfo.model";

export class UserBuilder {
	private user: User;
	private userInfo: UserInfo[];
	private third: Third;
	private thirdInfo: ThirdInfo[];	

	private status: string;
	private contrasena: string;
	private username: string;
	private idUserThirdPublic: string;

	

	public build(): Register {
		const register = new Register()
		register.user = this.user
		register.userInfo = this.userInfo
		register.third = this.third
		register.thirdInfo = this.thirdInfo
		register.status = this.status;
		register.contrasena = this.contrasena;
		register.username = this.username;
		register.idUserThirdPublic = this.idUserThirdPublic;
		return register
	}

	public buildWithuser(user: User): UserBuilder {
		this.user = user
		return this
	}
	public buildWithuserInfo(userInfo: UserInfo[]): UserBuilder {
		this.userInfo = userInfo
		return this
	}
	public buildWiththird(third: Third): UserBuilder {
		this.third = third
		return this
	}
	public buildWiththirdInfo(thirdInfo: ThirdInfo[]): UserBuilder {
		this.thirdInfo = thirdInfo
		return this
	}

	public buildStatus(status: string) {
		this.status = status;
		return this;
	}

	public buildContrasena(contrasena: string) {
		this.contrasena = contrasena;
		return this;
	}

	public buildUsername(username: string) {
		this.username = username;
		return this;
	}

	public buildIdUserThirdPublic(idUserThirdPublic: string) {
		this.idUserThirdPublic = idUserThirdPublic;
		return this;
	}
	
}