import { FactoryProvider, InjectionToken } from "@angular/core";
import { IRepository } from "../../../../domain/interfaces/irepository.interface";
import { ProfileSpesificUpSertService } from "./profilespesificupsert.service";
import { ProfileSpesific } from '../../../../domain/models/profile/profilespesific.model';

export const ProfileSpesificUpsertToken = new InjectionToken<IRepository<ProfileSpesific>>('profilespesificupsert.service');

export class ProfileSpesificUpSertProvider implements FactoryProvider {
	public provide: InjectionToken<IRepository<ProfileSpesific>> = ProfileSpesificUpsertToken;
	public deps: any[] = [ProfileSpesificUpSertService];
	public multi: boolean = false;
	public useFactory: Function = (...deps: IRepository<ProfileSpesific>[]) => {
		return deps[0]
	};
}