import { Component, OnInit, Inject } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialog, MatSnackBar, MatDialogRef } from '@angular/material';
import { CommerceEntity } from "../../../../../domain/models/register/commerceentity";
import { IRepository } from '../../../../../domain/interfaces/irepository.interface';
import { RootObject } from "../../../../../domain/models/register/rootObjectCatalog";
import { CatalogToken } from '../../../services/register/catalog.provider';
import { Content } from "../../../../../domain/models/register/contentCatalog.model";
import { catalog } from "../../../../../domain/models/register/catalog.model";

@Component({
  templateUrl: './rues-user.html',
  styleUrls: ['./rues-user.scss']
})
export class RuesUserComponent implements OnInit {
  public listActivities: Content[];
  public idActivitie: number;
  public fromActivitie: boolean = true;
  public commerceEntity = new CommerceEntity();

  constructor(private dialogRef: MatDialogRef<RuesUserComponent>,
    @Inject(CatalogToken) private serviceRegister: IRepository<catalog>) {
  }
  ngOnInit() {
     /**Listas de las 23 categorias */
     var catalogEntie = new catalog();
     catalogEntie.catalogId="112";
     this.serviceRegister.create(catalogEntie).subscribe(response => {
      this.listActivities = response.content;
    });
  }
  public isPersonJuridic(): void {
    this.dialogRef.close(this.idActivitie)
  }
  public isPersonNatu(): void {
    this.dialogRef.close(0)
  }

}