import {Observable} from 'rxjs';
import {RootObject} from '../../../../domain/models/register/rootObjectCatalog';
import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {IRepository} from '../../../../domain/interfaces/irepository.interface';
import {catalog} from '../../../../domain/models/register/catalog.model';
import {UrlPath} from '../../../../domain/enums/urlpath';

@Injectable()
export class CatalogService implements IRepository<catalog> {

  constructor(private http: HttpClient) {
  }

  public create(catalog: catalog): Observable<RootObject> {    
    var request= this.http.post<RootObject>(UrlPath.URL_PATH+`/servicebroker/v1/public/catalog/categorias/catalog/ `, catalog);
      // .post<Register>(`${AppSettings.Core().EndPoints.API}/userthird/create`, register);
     return request
  }

}
