import { Observable } from 'rxjs';
import { RootObject } from '../../../../domain/models/register/rootobject.model';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { IRepository } from '../../../../domain/interfaces/irepository.interface';
import { AppSettings } from '../../../../domain/appSettings';
import { FinantialModel } from '../../../../domain/models/profile/finantialmodel.model';
import {UrlPath} from '../../../../domain/enums/urlpath';

@Injectable()
export class FinantialService implements IRepository<FinantialModel> {

	constructor(private http: HttpClient) {
	}

	public create(values: FinantialModel): Observable<RootObject> {		
		var request = this.http.post<RootObject>(UrlPath.URL_PATH+`/servicebroker/v1/private/catalog/profile/finantial/`, values);
		return request;
	}

	public createList(values: FinantialModel[]): Observable<RootObject> {		
		var request = this.http.post<RootObject>(UrlPath.URL_PATH+`/servicebroker/v1/private/catalog/profile/finantial/listinfo/`, values);
		return request;
	}

}
