import { Component, ViewChild, OnInit, Inject } from '@angular/core';

import { FormBuilder, FormGroup, Validators } from '@angular/forms';

import { MatDialog, MatSnackBar, MatDialogRef } from '@angular/material';

import { UserBuilder } from '../../../../../domain/builders/user.model.builder';
import { Register } from '../../../../../domain/models/register/register.model';
import { Third } from '../../../../../domain/models/register/third.model';
import { ThirdInfo } from '../../../../../domain/models/register/thirdInfo.model';
import { User } from '../../../../../domain/models/register/user.model';
import { UserInfo } from '../../../../../domain/models/register/userInfo.model';
import { IRepository } from '../../../../../domain/interfaces/irepository.interface';
import { UserToken } from '../../../services/register/register.provider';
import { RootObject } from "../../../../../domain/models/register/rootobject.model";
import { Catalog } from '../../../../../domain/enums/Catalog.enum';
import { DialogComponent } from '../Dialog/DialogComponent';
import { Router } from '@angular/router';
import { RuesUserComponent } from '../rues/rues-user';

import { Observable } from 'rxjs';
import { debounceTime, map } from 'rxjs/operators';
import { CategoryRequest } from '../../../../../domain/models/register/categoryrequest.model';
import { CategoryCity } from '../../../../../domain/models/register/categorycity.model';
import { CategoryToken } from '../../../services/register/category.provider';
import { StatusToken } from '../../../services/register/status.provider';
import { WizardComponent } from 'angular-archwizard';
import { NgxSpinnerService } from "ngx-spinner";
import { CustomValidators } from '../../shared/custom-validators';

@Component({
  templateUrl: './create-user.html',
  styleUrls: ['./create-user.scss']
})
export class CreateRegisterComponent implements OnInit {
  @ViewChild('wizard', { read: false, static: false })
  public wizard: WizardComponent;
  public fg: FormGroup;
  public fgPswg: FormGroup;

  submitted = false;
  submittedpsw = false;

  public register = new Register;
  public third: Third = new Third();
  public thirdInfo: ThirdInfo[];
  public user: User = new User();
  public username: string = "";
  public password: string = "";
  public passwordConf: string = "";
  //	public emailThirdInfo= new ThirdInfo();
  public addresThirdInfo = new ThirdInfo();
  public cityThirdInfo = new ThirdInfo();

  public cityCategory = new CategoryRequest();
  public listCities: CategoryCity[];
  public fileNameDialogRef: MatDialogRef<DialogComponent>;
  public userInfo: UserInfo[];
  error: boolean = false;
  validateMail: boolean = false;


  constructor(private fb: FormBuilder, private fgPsw: FormBuilder,
    @Inject(UserToken) private serviceRegister: IRepository<Register>,
    private dialog: MatDialog, private router: Router,
    @Inject(StatusToken) private statusRegister: IRepository<Register>,
    @Inject(CategoryToken) private categoryService: IRepository<CategoryRequest>, private spinner: NgxSpinnerService) {

    this.callCities();


  }

  ngOnInit() {
    this.fg = this.fb.group({
      identification: ['', [Validators.required]],
      Name: ['', [Validators.required]],
      LastName: ['', [Validators.required]],
      Email: ['', Validators.compose([
        Validators.required,
        Validators.email]
      )
      ],
      Phone: ['', [Validators.required]],
      Nit: ['', [Validators.required]],
      businessName: ['', [Validators.required]],
      EmailFact: ['', [Validators.required, Validators.email]],
      Address: ['', [Validators.required]],
      Citie: ['', [Validators.required]],
      checkboxTerm: [false, Validators.requiredTrue],
    });
    this.fgPswg = this.fgPsw.group({
      username: ['', [Validators.required]],
      passwordUser: ['', Validators.compose([
        Validators.required,
        // check whether the entered password has a number
        CustomValidators.patternValidator(/\d/, {
          hasNumber: true
        }),
        // check whether the entered password has upper case letter
        CustomValidators.patternValidator(/[A-Z]/, {
          hasCapitalCase: true
        }),
        // check whether the entered password has a lower case letter
        CustomValidators.patternValidator(/[a-z]/, {
          hasSmallCase: true
        }),
        // check whether the entered password has a special character
        CustomValidators.patternValidator(
          /[ !@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]/,
          {
            hasSpecialCharacters: true
          }
        ),
        Validators.minLength(8)

      ])],
      passwordUserConf: [null, Validators.compose([Validators.required])]
    },
      {
        // check whether our password and confirm password match
        validator: CustomValidators.passwordMatchValidator
      }
    );
    this.w.passwordUser.errors.minLength

  }

  sameTextId(event: any) {
    var inputIdCompanie = document.getElementById("Nit") as HTMLInputElement;
    var inputId = document.getElementById("numDoc") as HTMLInputElement;
    var typeId = document.getElementById("typeDocComp") as HTMLSelectElement;
    if (typeId.value === 'Cedula de ciudadana') {
      inputIdCompanie.value = inputId.value;
    }
  }
  numberOnly(event): boolean {

    const charCode = (event.which) ? event.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      return false;
    }
    return true;
  }
  lettersOnly(event): boolean {
    const charCode = (event.which) ? event.which : event.keyCode;
    if (charCode > 32 && (charCode < 65 || charCode > 90) && (charCode < 97 || charCode > 122)) {
      return false;
    }
    return true;
  }

  onChange(deviceValue) {
    var inputIdCompanie = document.getElementById("Nit") as HTMLInputElement;
    var inputId = document.getElementById("numDoc") as HTMLInputElement;
    if (deviceValue === 'Cedula de ciudadana') {

      inputIdCompanie.value = inputId.value;
      (<HTMLInputElement>document.getElementById("Nit")).disabled = true;

    }
    else {
      inputIdCompanie.value = "";
      inputIdCompanie.disabled = false;

    }
  }
  is_edit: boolean = true;
  isDisabled(): boolean {
    return this.is_edit;
  }
  public createUser(): void {
    this.submitted = true;
    if (this.fg.invalid) {
      this.error = true;
      return;
    }
    else {

      if (!this.validateMail) {
        this.validateMail = true;
        this.error = true;
        return;
      }
    }
    var typeIdThird = document.getElementById("typeDoc") as HTMLSelectElement;
    var typeIdCompanie = document.getElementById("typeDocComp") as HTMLSelectElement;
    var typeDoc = null;
    var typeDocCompanie = null;
    switch (typeIdThird.value) {
      case 'Cedula de ciudadana':
        typeDoc = Catalog.ValueCedula
        break;
      case 'Cedula de extranjera':
        typeDoc = Catalog.ValueCCExt
        break;
    }
    switch (typeIdCompanie.value) {
      case 'NIT':
        typeDocCompanie = Catalog.ValueNit
        break;
      case 'NIT de extranjera':
        typeDocCompanie = Catalog.ValueNITExt
        break;
      case 'Cedula de ciudadana':
        typeDocCompanie = Catalog.ValueCedula
        break;
      case 'Cedula de extranjera':
        typeDocCompanie = Catalog.ValueCCExt
        break;
    }

    this.spinner.show();
    const ct = this.fg.controls
    //Usuario
    this.user.vIdUserPublic = "";
    this.user.vIdType = typeDoc;
    this.user.vIdentificationNumber = ct.identification.value;
    this.user.vName = ct.Name.value;
    this.user.vLastName = ct.LastName.value;
    this.user.vEmail = ct.Email.value;
    this.user.vPhone = ct.Phone.value;
    this.user.vProfilePicture = "foto";
    this.user.vEnable = 1;

    //userInfo
    var userInfoEntie = new UserInfo()
    userInfoEntie.vIdUserPublic = "",
      userInfoEntie.vIdInfoTypeUser = Catalog.Anonimo,
      userInfoEntie.vValue = "",
      userInfoEntie.vEnable = 1

    this.userInfo = [userInfoEntie];

    //third
    this.third.vIdThirdPublic = "";
    this.third.vIdType = typeDocCompanie;
    this.third.vIdentificationNumber = ct.Nit.value;
    this.third.vBusinessName = ct.businessName.value;
    this.third.vEnable = 1;
    this.third.idThird = null
    //thirdInfo
    //thirdInfo MAIL_FACTURACION("301002")
    var thirdInfoEntieMail = new ThirdInfo()
    thirdInfoEntieMail.idThirdInfioPrimaria = null;
    thirdInfoEntieMail.vidInfoTypeThird = Catalog.SeccionCampoMailFacturacion
    thirdInfoEntieMail.vidThirdPublic = ""
    thirdInfoEntieMail.idThird = null
    thirdInfoEntieMail.vvalue = ct.EmailFact.value
    thirdInfoEntieMail.venable = 1

    //thirdInfo DIRECCION_CORRESPONDENCIA("301003")
    var thirdInfoEntieAddress = new ThirdInfo()
    thirdInfoEntieAddress.idThirdInfioPrimaria = null;
    thirdInfoEntieAddress.vidInfoTypeThird = Catalog.SeccionCampoDireccionCorrespondencia
    thirdInfoEntieAddress.vidThirdPublic = ""
    thirdInfoEntieAddress.idThird = null
    thirdInfoEntieAddress.vvalue = ct.Address.value
    thirdInfoEntieAddress.venable = 1

    //thirdInfo CIUDAD("301004")
    var thirdInfoCitie = new ThirdInfo()
    thirdInfoCitie.idThirdInfioPrimaria = null;
    thirdInfoCitie.vidInfoTypeThird = Catalog.SeccionCampoCiudad
    thirdInfoCitie.vidThirdPublic = ""
    thirdInfoCitie.idThird = null
    thirdInfoCitie.vvalue = ct.Citie.value.codHijo
    thirdInfoCitie.venable = 1

    this.thirdInfo = [thirdInfoEntieMail, thirdInfoEntieAddress, thirdInfoCitie];
    //Usuario

    var register = new Register();
    register.user = this.user;
    register.userInfo = this.userInfo;
    register.third = this.third;
    register.thirdInfo = this.thirdInfo;

    //Todo
    var rootObject = new RootObject()
    rootObject.content = register
    rootObject.message = null
    rootObject.status = null

    var builder = new UserBuilder()
      .buildWithuser(this.user)
      .buildWithuserInfo(this.userInfo)
      .buildWiththird(this.third)
      .buildWiththirdInfo(this.thirdInfo)
      .build()
    this.serviceRegister.create(builder).subscribe(user => this.mapUser(user));

  }
  get f() { return this.fg.controls; }
  get w() { return this.fgPswg.controls; }
  private mapUser(base: RootObject): void {
    if (base.status === "CONFLICT") {
      this.spinner.hide();
      this.dialog.open(DialogComponent, { data: 2 }).afterClosed().subscribe(result => {
        this.router.navigate(['']);
      });
    }
    else if (base.content.status === Catalog.ValueEstatusActividades) {
      this.spinner.hide();
      this.dialog.open(DialogComponent, { data: 1 }).afterClosed().subscribe(result => {
        this.router.navigate(['']);
      });
    }
    else if (base.status === 'CREATED') {
      this.spinner.hide();
      this.dialog.open(DialogComponent, { data: 3 }).afterClosed().subscribe(result => {
        window.location.href = 'http://sso-bepartners-dev.apps.appcanvas.net/login';
      });
    }
    else {
      this.register = base.content;//Content contiene la informacion del registro con los Ids generados
      var ListUser = null;
      var ListThird = null;
      var RuesThird = null;

      base.content.userInfo.forEach(element => {
        if (element.vIdInfoTypeUser === Catalog.SeccionListasUsuario) {
          ListUser = element.vValue;
        }
      });
      base.content.thirdInfo.forEach(element => {
        switch (element.vidInfoTypeThird) {
          case Catalog.SeccionListasTercero: {
            ListThird = element.vvalue;
            break;
          }
          case Catalog.SeccionRuesTercero: {
            RuesThird = element.vvalue;
            break;
          }
        }

      });

      if (ListUser === Catalog.ValueListasErrorConsulta || ListThird === Catalog.ValueListasErrorConsulta || ListUser === Catalog.ValueListasReportado || ListThird === Catalog.ValueListasReportado) {
        this.spinner.hide();
        this.dialog.open(DialogComponent, { data: 2 }).afterClosed().subscribe(result => {
          this.router.navigate(['']);
        });
      }
      else if (ListUser === Catalog.ValueListasNoReportado && ListThird === Catalog.ValueListasNoReportado) {
        if (RuesThird === Catalog.ValueRuesInactivo || RuesThird === Catalog.ValueRuesSinResultado || RuesThird === Catalog.ValueRuesErrorConsulta) {
          this.spinner.hide();
          this.dialog
            .open(RuesUserComponent)
            .afterClosed()
            .subscribe(data => this.isPersonJuridic(data))
        }
        switch (RuesThird) {
          case Catalog.ValueRuesActivo: {
            this.spinner.hide();
            this.wizard.navigation.goToNextStep();
            break;
          }

        }
      }

    }
  }
  private isPersonJuridic(idActivitie: number): void {

    if (idActivitie != 0) {
      var economicActivity = new ThirdInfo();
      economicActivity.venable = 1;
      economicActivity.vidInfoTypeThird = Catalog.SeccionActividadEconomica;
      economicActivity.vidThirdPublic = "1231264";
      economicActivity.vvalue = idActivitie.toString();
      this.thirdInfo = [economicActivity];

      let buildStatusSend = new UserBuilder()
        .buildWithuser(this.register.user)
        .buildWithuserInfo(this.register.userInfo)
        .buildWiththird(this.register.third)
        .buildWiththirdInfo(this.register.thirdInfo)
        .buildIdUserThirdPublic(this.register.idUserThirdPublic)
        .buildStatus(Catalog.ValueEstatusActividades)
        .build();
      this.statusRegister.create(buildStatusSend).subscribe(user => this.mapUser(user));
    }
    else {
      window.open("https://www.midatacredito.com", "_blank");
      this.router.navigate(['']);
    }

  }
  /**Servicio para traer ciudades */

  private callCities() {
    this.cityCategory.idCategoriaType = "104002";
    this.cityCategory.nombreTree = "Region";
    this.categoryService.create(this.cityCategory).subscribe(response => {

      this.listCities = response.content;
    });
  }

  /**Inicio Auto-complete*/
  public formatter = (x: { nomHijo: string, nomPadre: string }) => x.nomHijo + ", " + x.nomPadre;



  search = (text$: Observable<string>) => text$.pipe(
    debounceTime(200),
    map(term => term === '' ? [] : this.listCities.filter(v => v.nomHijo.toLowerCase().indexOf(term.toLowerCase()) > -1).slice(0, 10))
  );
  /**Fin Auto-complete*/
  confirmarContrasena() {
    this.submittedpsw = true;
    if (this.fgPswg.invalid) {
      this.error = true;
      return;
    }

    console.log("Register Saved: Print");
    console.log(this.register);
    console.log(this.password + " " + this.passwordConf + "Result: " + (this.password == this.passwordConf));

    if (this.password === this.passwordConf) {
      let buildStatus = new UserBuilder()
        .buildWithuser(this.register.user)
        .buildWithuserInfo(this.register.userInfo)
        .buildWiththird(this.register.third)
        .buildWiththirdInfo(this.register.thirdInfo)
        .buildContrasena(this.password)
        .buildIdUserThirdPublic(this.register.idUserThirdPublic)
        .buildUsername(this.username)
        .buildStatus("109003")
        .build();
      this.statusRegister.create(buildStatus).subscribe(user => this.mapUser(user));
    }
  }
}
