import {Component, Injectable, AfterViewInit, Inject, OnInit} from '@angular/core';
import {LogOut} from '../../../domain/models/logout/logout';
import {Router, RouterStateSnapshot, RouterState, ActivatedRoute} from '@angular/router';
import {logoutoToken} from '../../app/services/sso/logout.provider';
import {IRepository} from '../../../domain/interfaces/irepository.interface';
import {User} from '../../../domain/models/register/user.model';
import {UserSessionToken} from '../../app/services/profile/usersession.provider';
import {UserSession} from '../../../domain/models/profile/usersession.model';
import {LoginGuard} from '../guards/login.guard';
import {ProfileRequest} from '../../../domain/models/profile/profilerequest.model';
import {HttpClient} from '@angular/common/http';

@Component({
  selector: 'is-login',
  templateUrl: './islogin.component.html',
  styleUrls: ['./islogin.component.scss']
})
export class isloginComponent implements OnInit {
  public photo: string;
  public Isloged: Boolean;
  public username: string;
  public route: RouterStateSnapshot;
  token: string;

  constructor(@Inject(UserSessionToken) private userSession: IRepository<User>, private router: Router, @Inject(logoutoToken) private logOut: IRepository<LogOut>, private guard: LoginGuard, private routeActive: ActivatedRoute) {
    this.Isloged = false;
  }

  ngOnInit() {
    this.routeActive.queryParams.subscribe(params => {
      this.token = params['to'];
      if (this.token != undefined || this.token != null) {
        localStorage.setItem('id_token', this.token);
      }
    });
    const state: RouterState = this.router.routerState;
    const snapshot: RouterStateSnapshot = state.snapshot;
    this.guard.canActivate(null, snapshot);

    localStorage.getItem('isLoggedIn') === 'true' ? this.Isloged = true : this.Isloged = false;


    if (this.Isloged) {
      var user = new User();
      user.vEmail = localStorage.getItem('currentUser');
      this.userSession.create(user).subscribe(response => {
          let userSession = new UserSession();
          userSession = response.content;
          /// Construction del Objeto
          const userProfileRequest = new ProfileRequest();
          userProfileRequest.user = user;
          userProfileRequest.vIdThirdPublic = userSession.idThirdPublic;
          userProfileRequest.user.vIdUserPublic = userSession.idUserPublic;
          userProfileRequest.user.vIdType = userSession.idType;
          userProfileRequest.user.vIdentificationNumber = userSession.usIdentificationNumber;
          userProfileRequest.user.vName = userSession.name;
          userProfileRequest.user.vLastName = userSession.lastName;
          this.photo = userSession.profilePicture;
          debugger
          this.username = user.vName;

         
          sessionStorage.setItem('userSession', JSON.stringify(userSession));
          sessionStorage.setItem('userProfileRequest', JSON.stringify(userProfileRequest));
          localStorage.setItem('photo', userSession.profilePicture);
          localStorage.setItem('porcentajePerfil', JSON.stringify('{ "plinked":0, "pfinantial":0, "pprofile":0, "indicadorConfianza":0 }'));
        },
        err => {
          return console.log(err);
        },
        () => console.log('yay'));
    }
  }

  openRegisterComponent() {
    this.router.navigate(['user/create']);
  }

  openLogIn() {
    this.router.navigate(['profile/home']);
  }

  logout() {
    var tokenUser = new LogOut();
    tokenUser.sessionToken = localStorage.getItem('id_token');
    this.logOut.create(tokenUser).subscribe(param => this.mapUserLogout(param));
  }

  private mapUserLogout(base: LogOut): void {
    localStorage.removeItem('id_token');
    localStorage.removeItem('currentUser');
    sessionStorage.removeItem('userSession');
    sessionStorage.removeItem('userProfileRequest');
    localStorage.removeItem('photo');
    localStorage.setItem('isLoggedIn', 'false');
    localStorage.removeItem('porcentajePerfil');
    this.Isloged = false;
    this.router.navigate(['']);
  }

  cambiarValor(valor) {
    this.Isloged = valor;
  }

}
