// tslint:disable: quotemark
import { Component, OnInit ,Inject} from '@angular/core';
import { ProfileBasicToken} from '../../../services/profile/profilebasic.provider';

import { ProfileRequest } from "../../../../../domain/models/profile/profilerequest.model";
import { ProfileBasic } from "../../../../../domain/models/profile/profilebasic.model";
import { IRepository } from '../../../../../domain/interfaces/irepository.interface';
import { Third } from "../../../../../domain/models/register/third.model";
import { User } from "../../../../../domain/models/register/user.model";
import { RootObject } from "../../../../../domain/models/register/rootobject.model";


import { ProfileBasicService } from "../../../services/profile/profilebasic.service";

import { FinantialModel } from '../../../../../domain/models/profile/finantialmodel.model';
import { CatalogFinantialInfoEnum } from 'src/domain/enums/CatalogFinantialInfoEnum';
import { FinantialInfo } from '../../../../../domain/models/profile/finantialinfo.model';
import { Finantial } from '../../../../../domain/models/profile/finantial.model';
import { CatalogFinantialTypesEnum } from 'src/domain/enums/CatalogFinantialTypeEnum';
import { FinantialToken } from '../../../services/profile/finantial.provider';
import {CommunicationService} from '../../../services/Communication.service';
import {UserSession} from '../../../../../domain/models/profile/usersession.model';
import {ActivatedRoute, Router} from '@angular/router';
import {UrlPath} from '../../../../../domain/enums/urlpath';
import {HttpClient} from '@angular/common/http';
import * as jwt_decode from 'jwt-decode';


@Component({
	selector: 'app-managewelcome',
	templateUrl: './managewelcome.component.html',
	styleUrls: ['./managewelcome.component.scss']
})
export class ManageWelcomeComponent implements OnInit {
  token: string;


	constructor(private http: HttpClient, private routeActive: ActivatedRoute, private router: Router) {

    this.routeActive.queryParams.subscribe(params => {
      this.token = params['to'];
      if (this.token != undefined || this.token != null) {
        localStorage.setItem('id_token', this.token);
      }
    });

    this.callData();

    console.log('temrinando constructor');

	}

  async callData() {

    // tslint:disable-next-line:prefer-const
    let user = new User();
    var decoded = jwt_decode(this.token);
    user.vEmail = decoded.sub;
    // tslint:disable-next-line:max-line-length


    await this.http.get<RootObject>(UrlPath.URL_PATH + '/servicebroker/v1/public/catalog/profile/person/username/' + user.vEmail + '/').toPromise().then(response => {
//debugger
      let userSession = new UserSession();
      userSession = response.content;
      /// Construction del Objeto
      const userProfileRequest = new ProfileRequest();
      userProfileRequest.user = user;
      userProfileRequest.vIdThirdPublic = userSession.idThirdPublic;
      userProfileRequest.user.vIdUserPublic = userSession.idUserPublic;
      userProfileRequest.user.vIdType = userSession.idType;
      userProfileRequest.user.vIdentificationNumber = userSession.usIdentificationNumber;
      userProfileRequest.user.vName = userSession.name;
      userProfileRequest.user.vLastName = userSession.lastName;
      sessionStorage.setItem('userSession', JSON.stringify(userSession));
      sessionStorage.setItem('userProfileRequest', JSON.stringify(userProfileRequest));
      localStorage.setItem('photo', userSession.profilePicture);
      localStorage.setItem('porcentajePerfil', JSON.stringify('{ "plinked":0, "pfinantial":0, "pprofile":0, "indicadorConfianza":0 }'));
      this.router.navigate(['gestorconsulta/gestorgndex']);
    });
  }



  ngOnInit() {

	}
}
