import { Observable } from 'rxjs';
import { RootObject } from '../../../../domain/models/register/rootobject.model';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { IRepository } from '../../../../domain/interfaces/irepository.interface';
import {UrlPath} from '../../../../domain/enums/urlpath';
import {User} from '../../../../domain/models/register/user.model';

@Injectable()
export class UserSessionService implements IRepository<User> {

  constructor(private http: HttpClient) {
  }

  public create(values: User): Observable<RootObject> {
    var request = this.http.get<RootObject>(UrlPath.URL_PATH+"/servicebroker/v1/public/catalog/profile/person/username/"+values.vEmail+"/");
    return request;
  }

  }

