import {Observable} from 'rxjs';
import {RootObject} from '../../../../domain/models/register/rootobject.model';
import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {IRepository} from '../../../../domain/interfaces/irepository.interface';
import {UrlPath} from '../../../../domain/enums/urlpath';
import {RequestHistorySearch} from '../../../../domain/models/plans/requesthistorysearch.model';

@Injectable()
export class HistorySearchService implements IRepository<RequestHistorySearch> {

  constructor(private http: HttpClient) {
  }

  public create(values: RequestHistorySearch): Observable<RootObject> {    
    var response = this.http.post<RootObject>(UrlPath.URL_PATH + `/servicebroker/v1/private/history/searchs/`, values);
    return response;
  }
}
