import {Component, Inject, OnInit} from '@angular/core';
import {IRepository} from '../../../../../domain/interfaces/irepository.interface';
import {Router} from '@angular/router';
import {ActivePlanToken} from '../../../services/plans/activeplans.provider';
import {RequestActivePlan} from '../../../../../domain/models/plans/requestactiveplan.model';
import {ActivePlan} from '../../../../../domain/models/plans/activeplan.model';
import {RequestPlan} from '../../../../../domain/models/plans/requestplan.model';
import {UserSession} from '../../../../../domain/models/profile/usersession.model';
import { CommunicationService } from 'src/client/app/services/Communication.service';


@Component({
  selector: 'app-resumencompra',
  templateUrl: './resumencompra.component.html',
  styleUrls: ['./resumencompra.component.css']
})
export class ResumenCompraComponent implements OnInit {

  public activePlans: ActivePlan[] = [];
  public activePlansTemp: ActivePlan[] = [];
  public requestActivePlan = new RequestActivePlan;
  public titleMorePlans: string;
  public userSession=new UserSession();

  constructor(@Inject(ActivePlanToken) private activePlanIRepository: IRepository<RequestActivePlan>, private router: Router, private comService: CommunicationService) {
    this.comService.change.subscribe(reload=>{
      this.loadActivePlans();
      console.log('refreshh',reload);
    });
  }

  private changeTitleNameMore() {
    this.titleMorePlans = 'VER TODOS LOS PLANES';
  }

  private changeNoTitleNameMore() {
    this.titleMorePlans = 'VER MENOS PLANES';
  }

  ngOnInit() {
  
    this.userSession=JSON.parse(sessionStorage.getItem('userSession'));
    this.requestActivePlan.vIdThirdPublic = this.userSession.idThirdPublic;

    this.loadActivePlans();
    this.changeTitleNameMore();


  }


  morePlans() {
    if (this.activePlans.length == this.activePlansTemp.length) {
      this.seeOnePlan();
      this.changeTitleNameMore();
    } else {
      this.seeAllPlans();
      this.changeNoTitleNameMore();
    }
  }

  private seeOnePlan() {
    this.activePlans = [];
    this.activePlans.push(this.activePlansTemp[0]);
  }

  private seeAllPlans() {
    this.activePlans = [];
    this.activePlans = this.activePlansTemp;
  }

  loadActivePlans() {
    var request = new RequestPlan();
    this.activePlanIRepository.create(this.requestActivePlan).subscribe(response => {
      this.activePlansTemp = response.content;
      sessionStorage.setItem('activePlans', JSON.stringify(this.activePlans));
      if (this.activePlansTemp != null && this.activePlansTemp.length > 0) {
        this.seeOnePlan();
      }
    });
  }


}
