import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {LayoutComponent} from './layout/layout/layout.component';

import {CreateRegisterComponent} from 'src/client/app/modules/user/create-user/create-user';
import {PcPlanComponent} from './modules/plan/pcplan/pcplan.component';
import {isloginComponent} from './layautshared/islogin.component';
import {CanActivate} from '@angular/router';
import {LoginGuard} from './guards/login.guard';

const routes: Routes = [
 // {path: '', loadChildren: './modules/home/home.module#HomeModule'},
  //{path: '', component: LayoutComponent, loadChildren: './modules/pages.module#PagesModule'},

  {path: '', component: LayoutComponent, loadChildren: './modules/home/home.module#HomeModule'},
  {path: 'IsLoged', component: isloginComponent, canActivate: [LoginGuard]},
  {path: 'user/create', component: CreateRegisterComponent},
  {path: 'products', component: LayoutComponent, loadChildren: './modules/plan/planmodule.module#PlanModule'},
  {path: 'gestorconsulta', component: LayoutComponent, loadChildren: './modules/gestorconsulta/gestorconsulta.module#GestorConsultaModule'},
  {path: 'profile/home', component: LayoutComponent, loadChildren: './modules/profile/profilehome.module#ProfileHomeModule'},
  {path: 'plan', component: LayoutComponent, loadChildren: './modules/plan/planmodule.module#PlanModule'},
  {path: 'payu', component: LayoutComponent, loadChildren: './modules/payu/payu.module#PayUModule'}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {
}
