import {Observable} from 'rxjs';
import {RootObject} from '../../../../domain/models/register/rootobject.model';
import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {IRepository} from '../../../../domain/interfaces/irepository.interface';
import {UrlPath} from '../../../../domain/enums/urlpath';
import {PrestaClicRequest} from '../../../../domain/prestaclic/prestaclicrequest.model';

@Injectable()
export class PrestaClicRequestService implements IRepository<PrestaClicRequest> {

  constructor(private http: HttpClient) {
  }

  public create(values: PrestaClicRequest): Observable<RootObject> {
    var response = this.http.post<RootObject>(UrlPath.URL_PATH + `/servicebroker/v1/private/catalog/product/pc/generate/`, values);
    return response;
  }
}
