import {FactoryProvider, InjectionToken} from "@angular/core";
import {IRepository} from "../../../../domain/interfaces/irepository.interface";

import {PrestaClicRequestService} from './prestaclicrequest.service';
import {PrestaClicRequest} from '../../../../domain/prestaclic/prestaclicrequest.model';

export const PrestaClicRequestToken = new InjectionToken<IRepository<PrestaClicRequest>>('prestaclicrequest.service');

export class PrestaClicRequestProvider implements FactoryProvider {
  public provide: InjectionToken<IRepository<PrestaClicRequest>> = PrestaClicRequestToken;
  public deps: any[] = [PrestaClicRequestService];
  public multi: boolean = false;
  public useFactory: Function = (...deps: IRepository<PrestaClicRequest>[]) => {
    return deps[0]
  };
}
