import {FactoryProvider, InjectionToken} from "@angular/core";
import {IRepository} from "../../../../domain/interfaces/irepository.interface";
import {RootObject} from "../../../../domain/models/register/rootobject.model";
import {CategoryService} from "./category.service";
import {CategoryRequest} from '../../../../domain/models/register/categoryrequest.model';

export const CategoryToken = new InjectionToken<IRepository<CategoryRequest>>('category.service');

export class CategoryProvider implements FactoryProvider {
  public provide: InjectionToken<IRepository<CategoryRequest>> = CategoryToken;
  public deps: any[] = [CategoryService];
  public multi: boolean = false;
  public useFactory: Function = (...deps: IRepository<CategoryRequest>[]) => {
    return deps[0]
  };
}