import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HomeRoutingModule } from './home-routing.module';
import { NgxYoutubePlayerModule } from 'ngx-youtube-player';
import { ParticlesModule } from 'angular-particle';


import { Index1Component } from './index1/index1.component';

import { boostBusinessComponent } from '../boost business/boostBusiness.component';
import { consultationbusinessComponent } from '../business consultation/consultationbusiness.component';
import { PricingComponent } from '../pricing/pricing.component';
import { TestiComponent } from '../testi/testi.component';
import { ContactComponent } from '../contact/contact.component';
import {PcPlansService} from '../../services/plans/pcplans.service';
import {PcPlansProvider} from '../../services/plans/pcplans.provider';
import { LandingComponent } from './landing/landing.component';
//import {ProfileComponent} from '../profile/profile.component';

import { SwiperModule } from 'ngx-swiper-wrapper';
import { SWIPER_CONFIG } from 'ngx-swiper-wrapper';
import { SwiperConfigInterface } from 'ngx-swiper-wrapper';

const DEFAULT_SWIPER_CONFIG: SwiperConfigInterface = {};


@NgModule({
  providers: [
    PcPlansService,
    new PcPlansProvider(),
    {
      provide: SWIPER_CONFIG,
      useValue: DEFAULT_SWIPER_CONFIG
    }
  ],
  declarations: [
	Index1Component,
	boostBusinessComponent,
	consultationbusinessComponent,
	PricingComponent,
	TestiComponent,
  ContactComponent,
  LandingComponent
//	,ProfileComponent
],
  imports: [
    CommonModule,
    NgxYoutubePlayerModule.forRoot(),
    HomeRoutingModule,
    ParticlesModule,
    SwiperModule
  ]
})
export class HomeModule { }

