import {FactoryProvider, InjectionToken} from "@angular/core";
import {IRepository} from "../../../../domain/interfaces/irepository.interface";

import {RequestActivePlan} from '../../../../domain/models/plans/requestactiveplan.model';
import {ActivePlansService} from './activeplans.service';

export const ActivePlanToken = new InjectionToken<IRepository<RequestActivePlan>>('activeplans.service');

export class ActivePlansProvider implements FactoryProvider {
  public provide: InjectionToken<IRepository<RequestActivePlan>> = ActivePlanToken;
  public deps: any[] = [ActivePlansService];
  public multi: boolean = false;
  public useFactory: Function = (...deps: IRepository<RequestActivePlan>[]) => {
    return deps[0]
  };
}
