import { Injectable, EventEmitter, Output } from '@angular/core';
import { Observable,Subject } from 'rxjs';
import { Example } from '../../../domain/models/example/RootObject';
import { RootObject } from "../../../domain/models/register/rootobject.model";


@Injectable()
export class CommunicationService {

	@Output() change: EventEmitter<boolean> = new EventEmitter();

	reload = false;

    constructor() {
    }
	private sub_updateTransaction = new Subject<Example>();
	public obs_updateTransaction: Observable<Example> = this.sub_updateTransaction.asObservable();

	public updateTransaction(transaction: Example) {
		this.sub_updateTransaction.next(transaction);
	}

	private sub_loadProfile = new Subject<RootObject>();
	public obs_loadProfile: Observable<RootObject> = this.sub_loadProfile.asObservable();

	public loadProfile(transaction: RootObject) {
		this.sub_loadProfile.next(transaction);
	}

	reloadComponent(){
		this.reload = !this.reload;
		this.change.emit(this.reload);
	}
}