import {FactoryProvider, InjectionToken} from "@angular/core";
import {IRepository} from "../../../../domain/interfaces/irepository.interface";

import {LogOut} from '../../../../domain/models/logout/logout';
import {LogOutService} from './logout.service';

export const logoutoToken = new InjectionToken<IRepository<LogOut>>('logout.service');

export class HistorySearchProvider implements FactoryProvider {
  public provide: InjectionToken<IRepository<LogOut>> = logoutoToken;
  public deps: any[] = [LogOutService];
  public multi: boolean = false;
  public useFactory: Function = (...deps: IRepository<LogOut>[]) => {
    return deps[0]
  };
}
