import { Component, Injectable, AfterViewInit, Inject } from '@angular/core';
import { ScrollSpyService } from 'ngx-scrollspy';
import { Router } from '@angular/router';
import { PrestaClicRequestToken } from '../../services/prestaclic/prestaclicrequest.provider';
import { IRepository } from '../../../../domain/interfaces/irepository.interface';
import { PrestaClicRequest } from '../../../../domain/prestaclic/prestaclicrequest.model';
import { User } from '../../../../domain/models/register/user.model';
import { UserSessionToken } from '../../services/profile/usersession.provider';
import { UserSession } from '../../../../domain/models/profile/usersession.model';
import { Routes, RouterModule, ActivatedRoute } from '@angular/router';

@Injectable()
@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})

export class HeaderComponent implements AfterViewInit {
  sections = {};
  token: string;
  constructor(@Inject(UserSessionToken) private userSession: IRepository<User>, private scrollSpyService: ScrollSpyService, private router: Router, private route: ActivatedRoute) {

  }


  ngOnInit() {
    this.route.queryParams.subscribe(params => {
      this.token = params['to'];
      if (this.token != undefined || this.token != null)
      {
        localStorage.setItem('id_token', this.token);
      }
    });

    window.onscroll = function () { myFunction() };
    var navbar1 = document.getElementById("navbar1");
    var imgHome = document.getElementById("imgHome");
    var imgLogoReducido = document.getElementById("imgLogoReducido");
    function myFunction() {
      if (document.body.scrollTop > 40 || document.documentElement.scrollTop > 40) {
        navbar1.style.backgroundColor = "white";
        navbar1.style.padding = "10px";
      }
      else {
        navbar1.style.backgroundColor = "";
        navbar1.style.padding = "20px";
      }
      if (document.body.scrollTop > 80 || document.documentElement.scrollTop > 80) {
        imgHome.style.visibility = "hidden"
        imgLogoReducido.style.visibility = "visible"
      }
      else {
        imgHome.style.visibility = "visible"
        imgLogoReducido.style.visibility = "hidden"

      }
    }
  }



  toggleMenu() {
    document.getElementById('navbarCollapse').classList.toggle('show');
    document.getElementById('menu_button').classList.toggle('collapsed');
  }

  ngAfterViewInit() {

    let sections: NodeListOf<Element> = document.querySelectorAll(".section");
    let self = this;

    Array.prototype.forEach.call(sections, function (e) {
      self.sections[e.id] = parseFloat(e.offsetTop);
    });

    this.scrollSpyService.getObservable('window').subscribe((e: any) => {
      let scrollPosition = document.documentElement.scrollTop;

      for (let menu in this.sections) {
        if (this.sections[menu] <= scrollPosition) {
          if (menu == "testi" || menu == "") { }
          else {

            document.getElementById('navbarCollapse').classList.remove('show');
          }
        }
      }
    });
  }


}



